import React, { Component, Suspense } from 'react';
import { Modal } from 'react-bootstrap';
import { v4 as uuidv4 } from 'uuid';
import Swal from 'sweetalert2';
import GetRightsCopyUrl from '../GetRightsModal/Elements/GetRightsSteps/GetRightsCopyUrl';
//import PerfectScrollbar from 'react-perfect-scrollbar'

const UploadedAssets = React.lazy(() => import('./Compontnets/UploadedAssets'));

const UploadAssets = React.lazy(() => import('./Compontnets/UploadAssets'));

class AddAssetsModal extends Component {
    constructor() {
        super();
    }
    state = {
        collection: null,
        showModalType: 'uploadAssets', isUploaded: false, uploadedData: [], selected: 0, checkAll: false,
        captionStatus: false,
        captionText: "",
        tagsStatus: false,
        tagstext: "",
        copyrightStatus: false,
        copyrightValue: false,
        collectionError: false,
        updateRequest: false
    };

    updateStatusC = event => {
        const { uploadedData, captionText } = this.state;
        uploadedData.map((item) => {
            //if (item.checked) {
            if (event.target.checked) item.caption = captionText
            else item.caption = item.caption
            // }
            // else {
            //     item.caption = item.caption
            // }
        })

        this.setState({
            captionStatus: event.target.checked, uploadedData
        })
    }
    updateStatusTag = event => {
        const { uploadedData, tagstext } = this.state;
        uploadedData.map((item) => {
            //if (item.checked) {
            if (event.target.checked) item.addTags = tagstext
            else item.addTags = item.addTags
            // }
            //     else {
            //         item.addTags = item.addTags
            //     }
        })
        console.log("updateStatusTag", uploadedData)
        this.setState({
            tagsStatus: event.target.checked, uploadedData
        })
    }
    updateStatusCopy = event => {
        const { uploadedData, copyrightValue } = this.state;
        uploadedData.map((item) => {
            ///if (item.checked) {
            if (event.target.checked) item.copyright = copyrightValue
            else item.copyright = item.copyright
            // }
            // else {
            //     item.copyright = item.copyright
            // }
        })

        this.setState({
            copyrightStatus: event.target.checked, uploadedData
        })
    }

    onChangeCaption = event => {
        const { uploadedData, captionStatus } = this.state;
        uploadedData.map((item) => {
            //if (item.checked) {
            if (captionStatus) item.caption = event.target.value
            else item.caption = item.caption
            // }
            // else {
            //     item.caption = item.caption
            // }
        })
        this.setState({ captionText: event.target.value, uploadedData })
    }

    onLoadAddCaption = () => {
        var { uploadedData, captionText, captionStatus } = this.state;

        if (captionStatus) {
            uploadedData.map((item, index) => {
                if (item.checked) {
                    uploadedData[index].caption = captionText
                    //item.caption = captionText
                }
                else {
                    uploadedData[index].caption = ""
                }
            })
            this.setState({ uploadedData })
        }


    }

    onChangeTags = (value) => {


        const { uploadedData, tagsStatus } = this.state;
        uploadedData.map((item) => {
            //if (item.checked) {
            if (tagsStatus) item.addTags = value
            else item.addTags = item.addTags
            // }
            // else {
            //     item.caption = item.caption
            // }
        })
        this.setState({ tagstext: value, uploadedData })
    }
    onLoadTags = () => {
        var { uploadedData, tagstext, tagsStatus } = this.state;
        if (tagsStatus) {
            uploadedData.map((item, index) => {
                if (item.checked) {

                    uploadedData[index].addTags = tagstext
                    //item.addTags = tagstext
                }
                else {
                    uploadedData[index].addTags = ""
                }
            })
        }

        this.setState({ uploadedData })
    }
    onChangeCopyRight = (value) => event => {
        const { uploadedData, copyrightStatus } = this.state;
        uploadedData.map((item) => {
            if (item.checked) {
                if (copyrightStatus) item.copyright = value
                else item.copyright = false
            }
            else {
                item.copyright = false
            }
        })
        this.setState({ copyrightValue: value, uploadedData })
    }
    onLoadCopyRight = () => {

        var { uploadedData, copyrightValue, copyrightStatus } = this.state;
        if (copyrightStatus) {
            uploadedData.map((item, index) => {
                if (item.checked) {
                    //item.copyright = copyrightValue
                    uploadedData[index].copyright = copyrightValue
                }
                else {
                    uploadedData[index].copyright = false
                }
            })
            this.setState({ uploadedData })
        }

    }

    handleClose = () => {
        const { uploadedData } = this.state
        if (uploadedData && uploadedData.length > 0) {
            Swal.fire({
                title: 'Are you sure?',
                text: "You want to  discard changes!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: 'Yes, discard changes!'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.props.editMediaHandleCallback(false)
                }
            })
        }
        else {
            this.props.editMediaHandleCallback(false)
        }

    };

    showModalTypeCallback = (showModalType) => {
        this.setState({
            showModalType: showModalType
        })
    }
    isUploaded = () => {
        const { collection } = this.state
        this.setState({
            isUploaded: !this.state.isUploaded
        })
    }
    onUploadedData = (newObject) => {
        const { uploadedData } = this.state;
        uploadedData.push(newObject)
        this.setState({ uploadedData }, () => this.onloadCheckedPostCount())
    }
    onClearUPloadObject = (data) => {
        this.setState({ uploadedData: data }, () => this.onloadCheckedPostCount())
    }
    onSelectedCollection = (newObject) => {
        this.setState({ collection: newObject, collectionError: false })
    }

    onClickTORemovePost = (postIndex) => event => {
        let { uploadedData } = this.state;

        if (postIndex > -1) {
            uploadedData.splice(postIndex, 1);
        }
        console.log("postIndex", postIndex, uploadedData)
        this.setState({ uploadedData }, () => this.onloadCheckedPostCount())
    }
    onClickTOCheckPost = (postIndex) => event => {
        let { uploadedData, captionText, tagstext, copyrightValue, captionStatus, tagsStatus, copyrightStatus } = this.state;


        if (postIndex > -1 && uploadedData[postIndex]) {
            if (uploadedData[postIndex].checked) {
                uploadedData[postIndex].checked = false
                uploadedData[postIndex].caption = ""
                uploadedData[postIndex].addTags = ""
                uploadedData[postIndex].copyright = false
            }
            else {
                uploadedData[postIndex].checked = true
                if (captionStatus) uploadedData[postIndex].caption = captionText
                if (tagsStatus) uploadedData[postIndex].addTags = tagstext
                if (copyrightStatus) uploadedData[postIndex].copyright = copyrightValue
            }
        }
        this.setState({ uploadedData }, () => this.onloadCheckedPostCount())
    }

    onloadCheckedPostCount = () => {
        const { uploadedData } = this.state;
        var count = uploadedData.filter((item) => item.checked == true).length;
        this.setState({ selected: count })
    }
    onClickToAllCheck = event => {
        const { uploadedData, checkAll, captionText, tagstext, copyrightValue, captionStatus, tagsStatus, copyrightStatus } = this.state;
        uploadedData.map((item) => {
            item.checked = !checkAll
            if (!checkAll == true) {
                if (captionStatus) item.caption = captionText
                if (tagsStatus) item.addTags = tagstext
                if (copyrightStatus) item.copyright = copyrightValue
            }
            else {
                item.caption = ""
                item.addTags = ""
                item.copyright = ""
            }

        })


        this.setState({
            uploadedData, selected: uploadedData.length, checkAll: !checkAll
        })
    }

    onCaptionSingle = (postIndex, value) => {
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {

            uploadedData[postIndex].captionSubmit = value
            this.setState({ uploadedData, updateRequest: !updateRequest })
        }

    }
    onSubmitCaption = (postIndex, value) => {
        let { uploadedData, updateRequest } = this.state;

        if (postIndex > -1 && uploadedData[postIndex]) {
            uploadedData[postIndex].caption = value
            console.log("onCaptionSingle", postIndex, uploadedData[postIndex].captionSubmit, value)
            this.setState({ uploadedData, updateRequest: !updateRequest })
        }

    }

    onResetCaption = (postIndex, value) => {
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {
            console.log("onResetCaption", postIndex, value)
            uploadedData[postIndex].caption = ""
            this.setState({ uploadedData, updateRequest: !updateRequest })
        }

    }
    onAddTagSingle = (postIndex, value) => {
        console.log("onAddTagSingle")
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {
            uploadedData[postIndex].addTagsSubmit = value
            this.setState({ uploadedData, updateRequest: !updateRequest })
        }
    }
    onAddTagReset = (postIndex, value) => {
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {
            uploadedData[postIndex].addTags = ""
            this.setState({ uploadedData, updateRequest: !updateRequest })
        }

    }
    onAddTagSubmit = (postIndex, value) => {
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {

            uploadedData[postIndex].addTags = value

            this.setState({ uploadedData, updateRequest: !updateRequest })
        }

    }
    onCopyRightSingle = (postIndex, value) => event => {
        let { uploadedData, updateRequest } = this.state;
        if (postIndex > -1 && uploadedData[postIndex]) {
            uploadedData[postIndex].copyright = value
        }
        this.setState({ uploadedData, updateRequest: !updateRequest })
    }
    render() {
        const { editMediaToggle, history } = this.props
        const { isUploaded, uploadedData, collection } = this.state


        return <Modal size={'full'} show={editMediaToggle} className="addAssetsModal border-0 p-0" backdrop={false}>
            <Modal.Header className="border-0 p-0 w-100">
                <div className="d-flex justify-content-between align-items-center px-lg-3 px-2 bg-white w-100">
                    {!isUploaded ?
                        <div className="addAssetsTitle d-flex align-items-center flex-shrink-1">
                            <h1 className="text-dark fw-semibold fs-6 mb-0">Choose Source</h1>
                        </div>
                        :
                        <div className="addAssetsTitle d-flex align-items-center flex-shrink-1">
                            <button type="button" className="btn btn-icon btn-sm me-2" onClick={() => this.isUploaded()}>
                                <span className="svg-icon text-dark">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M7.828 11H20v2H7.828l5.364 5.364-1.414 1.414L4 12l7.778-7.778 1.414 1.414z" fill="currentColor" />
                                    </svg>
                                </span>
                            </button>
                            <h1 className="text-dark fw-semibold fs-6 mb-0">Fill required assets information</h1>
                        </div>
                    }
                    <div className="d-flex align-items-center">
                        <button type="button" className="btn-close text-light" onClick={this.handleClose}></button>
                    </div>
                </div>

            </Modal.Header>
            <Modal.Body className="p-0 d-flex align-items-start border-0 flex-wrap w-100">
                <div className="assetsModalScroll w-100">
                    {!isUploaded ?
                        <Suspense fallback={<></>}><UploadAssets getAssetsCallback={this.isUploaded} onUploadedData={this.onUploadedData} onClearUPloadObject={this.onClearUPloadObject} uploadedData={this.state.uploadedData} onSelectedCollection={this.onSelectedCollection} collection={collection} collectionError={this.state.collectionError} editMediaHandleCallback={this.props.editMediaHandleCallback} /></Suspense>
                        :
                        <Suspense fallback={<></>}><UploadedAssets uploadedData={uploadedData} length={uploadedData.length} selected={this.state.selected} onClickTORemovePost={this.onClickTORemovePost} onClickTOCheckPost={this.onClickTOCheckPost} updateStatusC={this.updateStatusC} updateStatusTag={this.updateStatusTag} updateStatusCopy={this.updateStatusCopy} captionStatus={this.state.captionStatus} tagsStatus={this.state.tagsStatus} copyrightStatus={this.state.copyrightStatus} onChangeCaption={this.onChangeCaption} updateRequest={this.state.updateRequest}
                            onChangeTags={this.onChangeTags}
                            onChangeCopyRight={this.onChangeCopyRight} captionText={this.state.captionText} tagstext={this.state.tagstext} copyrightValue={this.state.copyrightValue} onClickToAllCheck={this.onClickToAllCheck} checkAll={this.state.checkAll} collection={collection} history={history} editMediaHandleCallback={this.props.editMediaHandleCallback} onCaptionSingle={this.onCaptionSingle} onAddTagSingle={this.onAddTagSingle} onCopyRightSingle={this.onCopyRightSingle} onAddTagSubmit={this.onAddTagSubmit} onSubmitCaption={this.onSubmitCaption} onAddTagReset={this.onAddTagReset} onResetCaption={this.onResetCaption} /></Suspense>
                    }
                </div>
            </Modal.Body>
        </Modal>
    }
}

export default AddAssetsModal;
